import propTypes from 'prop-types'
import styled from 'styled-components'
import { space, width, textAlign, color } from 'styled-system'

const Box = styled.div`
  ${space} ${width} ${textAlign}
  ${color}
`

Box.displayName = 'Box'

Box.propTypes = {
  ...space.propTypes,
  ...width.propTypes,
  color: propTypes.string,
  bg: propTypes.string,
  ...textAlign.propTypes
}

export default Box
